                      </v-btn>
                      <v-spacer></v-spacer>
                    </v-row>
                    <v-row v-if="mobilemedia.type === 2">
                      <v-spacer></v-spacer>
                      <v-file-input
                        style="width: 165px;"
                        label="UPLOAD VIDEO"
                        filled
                        @change="atualizaMetaPhases"
                        v-model="mobilemedia.object"
                        accept="video/*"
                        prepend-icon="mdi-video"
                      ></v-file-input>
                      <v-btn icon="icon" class="mt-2 ml-3">
                        <v-icon
                          x-large
