<script>
import Api from "@/services/Api";
import * as firebase from "firebase";

export default {
  name: "ProcedureDialog",
  props: ["dialog", "procedure", "concept", "module", "domain"],
  data: () => ({
    valid: true,
    dialog_alert: false,
    auxItemInfo: "",
    phasesControl: [],
    linkUrlRules: [(v) => !!v || " necessrio inserir a url do seu link"],
    phasesControlRules: [
      (v) =>
        !!v ||
        " necessrio inserir algum contedo nessa etapa do procedimento",
    ],
    procedureName: "",
    procedureNameRules: [
      (v) => !!v || " necessrio descrever o nome do procedimento",
      (v) =>
        (v && v.length <= 100) ||
        "Nome do procedimento deve ter no mximo 25 caracteres",
    ],
    procedureDescription: "",
    procedureDescriptionRules: [
      (v) =>
        !!v ||
        " necessrio inserir uma descrio do procedimento a ser inserido",
    ],
    mobileMediasControl: [],
    itemsMobileMedia: [
      {
        icon: "mdi-file-image",
        name: "Imagem",
      },
      {
        icon: "mdi-file-video",
        name: "Video",
      },
      {
        icon: "mdi-file-music",
        name: "Audio",
      },
      {
        icon: "mdi-link-variant",
        name: "Link",
      },
    ],
  }),
  watch: {
    dialog: function () {
      this.getProcedure();
    },
  },
  mounted: function () {
    this.getProcedure();
    this.$refs.form.resetValidation();
  },
  methods: {
    async getProcedure() {
      var vm = this;
      this.$nextTick(function () {
        vm.procedureName = vm.procedure.nameinformationitem;
        vm.procedureDescription = vm.procedure.descriptioninformationitem;
      });
      this.phasesControl = [];
      this.mobileMediasControl = [];
      if (this.procedure !== "") {
        this.procedure.phaseprocedures.forEach(async function (
          phase,
          indexPhase
        ) {
          await vm.phasesControl.push({
            description: phase.description,
            mobileMedias: [],
            url: phase.url,
          });
          if (phase.mobilemedias) {
            await phase.mobilemedias.forEach(async function (
              elementMobile,
              indexMobile
            ) {
              if (elementMobile.fk_idmediatype.split("/")[4] === "1") {
                vm.getSrcImagePhases(
                  indexPhase,
                  indexMobile,
                  elementMobile.path,
                  elementMobile.namefile
                );
                await vm.phasesControl[indexPhase].mobileMedias.push({
                  type: parseInt(elementMobile.fk_idmediatype.split("/")[4]),
                  object: null,
                  resolution: elementMobile.resolution,
                  path: elementMobile.path,
                  namefile: elementMobile.namefile,
                  url: elementMobile.url,
                });
              } else if (elementMobile.fk_idmediatype.split("/")[4] === "2") {
                vm.getSrcVideoQuestions(
                  indexPhase,
                  indexMobile,
                  elementMobile.path,
                  elementMobile.namefile
                );
                await vm.phasesControl[indexPhase].mobileMedias.push({
                  type: parseInt(elementMobile.fk_idmediatype.split("/")[4]),
                  object: null,
                  resolution: elementMobile.resolution,
                  time: elementMobile.time,
                  path: elementMobile.path,
                  namefile: elementMobile.namefile,
                  url: elementMobile.url,
                });
              } else if (elementMobile.fk_idmediatype.split("/")[4] === "3") {
                vm.getSrcAudioQuestions(
                  indexPhase,
                  indexMobile,
                  elementMobile.path,
                  elementMobile.namefile
                );
                await vm.phasesControl[indexPhase].mobileMedias.push({
                  type: parseInt(elementMobile.fk_idmediatype.split("/")[4]),
                  object: null,
                  path: elementMobile.path,
                  namefile: elementMobile.namefile,
                  url: elementMobile.url,
                });
              } else if (elementMobile.fk_idmediatype.split("/")[4] === "5") {
                await vm.phasesControl[indexPhase].mobileMedias.push({
                  type: parseInt(elementMobile.fk_idmediatype.split("/")[4]),
                  object: elementMobile.urllink,
                  url: elementMobile.url,
                });
              }
            });
          }
        });

        await this.procedure.mobilemedias.forEach(async function (
          elementMobile,
          indexMobile
        ) {
          if (elementMobile.fk_idmediatype.split("/")[4] === "1") {
            vm.getSrcImage(
              indexMobile,
              elementMobile.path,
              elementMobile.namefile
            );
            await vm.mobileMediasControl.push({
              type: parseInt(elementMobile.fk_idmediatype.split("/")[4]),
              object: null,
              resolution: elementMobile.resolution,
              path: elementMobile.path,
              namefile: elementMobile.namefile,
              url: elementMobile.url,
            });
          } else if (elementMobile.fk_idmediatype.split("/")[4] === "2") {
            vm.getSrcVideo(
              indexMobile,
              elementMobile.path,
              elementMobile.namefile
            );
            await vm.mobileMediasControl.push({
              type: parseInt(elementMobile.fk_idmediatype.split("/")[4]),
              object: null,
              resolution: elementMobile.resolution,
              time: elementMobile.time,
              path: elementMobile.path,
              namefile: elementMobile.namefile,
              url: elementMobile.url,
            });
          } else if (elementMobile.fk_idmediatype.split("/")[4] === "3") {
            vm.getSrcAudio(
              indexMobile,
              elementMobile.path,
              elementMobile.namefile
            );
            await vm.mobileMediasControl.push({
              type: parseInt(elementMobile.fk_idmediatype.split("/")[4]),
              object: null,
              path: elementMobile.path,
              namefile: elementMobile.namefile,
              url: elementMobile.url,
            });
          } else if (elementMobile.fk_idmediatype.split("/")[4] === "5") {
            await vm.mobileMediasControl.push({
              type: parseInt(elementMobile.fk_idmediatype.split("/")[4]),
              object: elementMobile.urllink,
              url: elementMobile.url,
            });
          }
        });
      }
    },
    async altera_Cria_Procedimento() {
      var vm = this;
      if (this.procedure === "") {
        await Api()
          .post(`/informationitem/`, {
            nameinformationitem: this.procedureName,
            descriptioninformationitem: this.procedureDescription,
            fk_informationitemtype: `/informationitemtype/4/`,
            fk_idconcept: this.concept.url,
            visible: true,
          })
          .then(async function (resposta) {
            vm.auxItemInfo = resposta.data.url;
            var auxPath = "";
            if (vm.mobileMediasControl) {
              await vm.mobileMediasControl.forEach(async (mobilemedia) => {
                if (mobilemedia.url) {
                  if (mobilemedia.type !== 5) {
                    firebase
                      .storage()
                      .ref()
                      .child(mobilemedia.path)
                      .put(mobilemedia.object);
                  }
                } else {
                  auxPath =
                    vm.domain.idknowledgedomain.toString() +
                    "/" +
                    (await setTimeout(async function () {
                      await Date.now().toString();
                    }, 1));
                  if (mobilemedia.type !== 5) {
                    firebase
                      .storage()
                      .ref()
                      .child(auxPath)
                      .put(mobilemedia.object);

                    mobilemedia.path = auxPath;
                  }
                }
              });
            }
            if (vm.mobileMediasControl) {
              var i = 0;
              await vm.mobileMediasControl.forEach(async function (
                mobilemedia,
                indexmobile
              ) {
                /* DANDO PUT NOS MOBILEMEDIAS QUE POSSUEM URL*/
                if (mobilemedia.url) {
                  if (mobilemedia.type === 1) {
                    await Api().put(mobilemedia.url, {
                      label: "Imagem " + (indexmobile + 1),
                      fk_idmediatype: "/mediatype/" + mobilemedia.type + "/",
                      path: mobilemedia.path,
                      resolution: mobilemedia.resolution,
                      namefile: mobilemedia.namefile,
                      description:
                        "Imagem " +
                        (indexmobile + 1) +
                        " " +
                        resposta.data.label,
                      time: null,
                      textfull: null,
                      textshort: null,
                      urllink: null,
                      difficultyLevel: null,
                      learningStyle: null,
                      fk_informationitem: resposta.data.url,
                    });
                  } else if (mobilemedia.type === 2) {
                    await Api().put(mobilemedia.url, {
                      label: "Video " + (indexmobile + 1),
                      fk_idmediatype: "/mediatype/" + mobilemedia.type + "/",
                      path: mobilemedia.path,
                      resolution: mobilemedia.resolution,
                      namefile: mobilemedia.namefile,
                      description: null,
                      time: mobilemedia.time,
                      textfull: null,
                      textshort: null,
                      urllink: null,
                      difficultyLevel: null,
                      learningStyle: null,
                      fk_informationitem: resposta.data.url,
                    });
                  } else if (mobilemedia.type === 3) {
                    await Api().put(mobilemedia.url, {
                      label: "Audio " + (indexmobile + 1),
                      fk_idmediatype: "/mediatype/" + mobilemedia.type + "/",
                      path: mobilemedia.path,
                      resolution: null,
                      namefile: mobilemedia.namefile,
                      description: null,
                      time: null,
                      textfull: null,
                      textshort: null,
                      urllink: null,
                      difficultyLevel: null,
                      learningStyle: null,
                      fk_informationitem: resposta.data.url,
                    });
                  } else if (mobilemedia.type === 5) {
                    await Api().put(mobilemedia.url, {
                      label: "Link " + (indexmobile + 1),
                      fk_idmediatype: "/mediatype/" + mobilemedia.type + "/",
                      path: null,
                      resolution: null,
                      namefile: null,
                      description: null,
                      time: null,
                      textfull: null,
                      textshort: null,
                      urllink: mobilemedia.object,
                      difficultyLevel: null,
                      learningStyle: null,
                      fk_informationitem: resposta.data.url,
                    });
                  }
                } else {
                  /* DANDO POST NOS MOBILEMEDIAS QUE NO POSSUEM URL*/
                  console.log(mobilemedia);
                  if (mobilemedia.type === 1) {
                    console.log("asdfsdfsd");
                    await Api().post(`/mobilemedia/`, {
                      label: "Imagem " + (indexmobile + 1),
                      fk_idmediatype: "/mediatype/" + mobilemedia.type + "/",
                      path: mobilemedia.path,
                      resolution: mobilemedia.resolution,
                      namefile: mobilemedia.path.split("/")[1],
                      description:
                        "Imagem " +
                        (indexmobile + 1) +
                        " " +
                        resposta.data.label,
                      time: null,
                      textfull: null,
                      textshort: null,
                      urllink: null,
                      difficultyLevel: null,
                      learningStyle: null,
                      fk_informationitem: resposta.data.url,
                    });
                  } else if (mobilemedia.type === 2) {
                    console.log("i", i);
                    await Api().post(`/mobilemedia/`, {
                      label: "Video " + (indexmobile + 1),
                      fk_idmediatype: "/mediatype/" + mobilemedia.type + "/",
                      path: mobilemedia.path,
                      resolution: mobilemedia.resolution,
                      namefile: mobilemedia.path.split("/")[1],
                      description: null,
                      time: mobilemedia.time,
                      textfull: null,
                      textshort: null,
                      urllink: null,
                      difficultyLevel: null,
                      learningStyle: null,
                      fk_informationitem: resposta.data.url,
                    });
                  } else if (mobilemedia.type === 3) {
                    await Api().post(`/mobilemedia/`, {
                      label: "Audio " + (indexmobile + 1),
                      fk_idmediatype: "/mediatype/" + mobilemedia.type + "/",
                      path: mobilemedia.path,
                      resolution: null,
                      namefile: mobilemedia.path.split("/")[1],
                      description: null,
                      time: null,
                      textfull: null,
                      textshort: null,
                      urllink: null,
                      difficultyLevel: null,
                      learningStyle: null,
                      fk_informationitem: resposta.data.url,
                    });
                  } else if (mobilemedia.type === 5) {
                    await Api().post(`/mobilemedia/`, {
                      label: "Link " + (indexmobile + 1),
                      fk_idmediatype: "/mediatype/" + mobilemedia.type + "/",
                      path: null,
                      resolution: null,
                      namefile: null,
                      description: null,
                      time: null,
                      textfull: null,
                      textshort: null,
                      urllink: mobilemedia.object,
                      difficultyLevel: null,
                      learningStyle: null,
                      fk_informationitem: resposta.data.url,
                    });
                  }
                }
              });
            }
          });
      } else {
        await Api()
          .put(`/informationitem/` + this.procedure.idinformationitem + `/`, {
            nameinformationitem: this.procedureName,
            descriptioninformationitem: this.procedureDescription,
            fk_informationitemtype: `/informationitemtype/4/`,
            fk_idconcept: this.concept.url,
          })
          .then(async function (resposta) {
            vm.auxItemInfo = resposta.data.url;
            var auxPath = "";
            if (vm.mobileMediasControl) {
              await vm.mobileMediasControl.forEach(async (mobilemedia) => {
                if (mobilemedia.url) {
                  if (mobilemedia.type !== 5) {
                    firebase
                      .storage()
                      .ref()
                      .child(mobilemedia.path)
                      .put(mobilemedia.object);
                  }
                } else {
                  auxPath =
                    vm.domain.idknowledgedomain.toString() +
                    "/" +
                    (await setTimeout(async function () {
