<template>
  <v-container class="pa-0">
    <v-card>
      <v-card-title style="background-color:#63B0B0; color:white;">
        <span class="headline">
          <p v-if="submodule">Edite o submdulo selecionado.</p>
          <p v-else>Defina o submdulo do domnio do conhecimento.</p>
        </span>
      </v-card-title>
      <v-card-text>
        <v-form ref="form" v-model="valid" lazy-validation="lazy-validation">
          <v-text-field
            v-model="subModuloTitle"
            :rules="subModuloTitleRules"
            label="Ttulo do submdulo"
            required="required"
          ></v-text-field>

          <v-text-field
            v-model="subModuloSubtitle"
            label="Subttulo do submdulo"
          ></v-text-field>
        </v-form>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="red" height="49" dark="dark" large="large" @click="reset">
          Cancelar
          <v-icon dark="dark" right="right">mdi-close</v-icon>
        </v-btn>
        <v-btn
          color="success"
          height="49"
          dark="dark"
          large="large"
          @click="validate"
        >
          Salvar
          <v-icon dark="dark" right="right">mdi-content-save</v-icon>
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-container>
</template>

<script>
  import Api from "@/services/Api";
  export default {
    name: "SubModuloDialog",
    props: ["domain", "module", "submodule", "dialog"],
    data: () => ({
      valid: true,
      subModuloTitle: "",
      subModuloTitleRules: [
        (v) => !!v || " necessrio descrever o ttulo do submdulo",
        (v) =>
          (v && v.length <= 40) ||
          "O ttulo do mdulo deve ter no mximo 40 caracteres",
      ],
      subModuloSubtitle: "",
      select: null,
      checkbox: false,
      modulos: "",
    }),
    watch: {
      dialog: function() {
        this.getSubModulo();
      },
      submodule: function() {
        this.getSubModulo();
      },
    },
    mounted: function() {
      this.getSubModulo();
      this.$refs.form.resetValidation();
    },
    methods: {
      getSubModulo() {
        var vm = this;
        this.$nextTick(function() {
          vm.subModuloTitle = vm.submodule.namemodule;
          vm.subModuloSubtitle = vm.submodule.subtitle;
        });
      },
      async postSubModulo() {
        await Api().post(`/module/`, {
          fk_idmodule: this.module.url,
          namemodule: this.subModuloTitle,
          subtitle: this.subModuloSubtitle,
          idknowledgedomain: this.domain.url,
        });
      },
      async putSubModulo() {
        await Api().put("/module/" + this.submodule.idmodule + "/", {
          fk_idmodule: this.module.url,
          namemodule: this.subModuloTitle,
          subtitle: this.subModuloSubtitle,
          idknowledgedomain: this.domain.url,
        });
      },
      async validate() {
        if (this.$refs.form.validate()) {
          this.$refs.form.validate();
          if (this.submodule === "") {
            await this.postSubModulo();
          } else {
            await this.putSubModulo();
          }
          this.$emit("close_or_save", "save");
          this.$refs.form.reset();
        }
      },

      reset() {
        this.$emit("close_or_save", "close");
        this.$refs.form.reset();
      },
      resetValidation() {
        this.$refs.form.resetValidation();
      },
    },
  };
</script>
